html {
  font-size: 62.5%;
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.body {
  margin: 0;
  padding: 0;
  background-color: #ccc; }

img {
  max-width: 100%;
  vertical-align: middle; }

.link {
  text-decoration: none;
  color: #0A3163; }
  .link:active, .link:focus {
    color: #0A3163; }

.button {
  cursor: pointer;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 1.4rem;
  border-radius: 30px;
  line-height: 40px;
  box-shadow: none;
  border: none;
  height: 40px;
  padding: 0 33px;
  color: white;
  background-color: #FF8617; }
  .button:focus {
    outline: none; }

.button--blue {
  background-color: #0A3163;
  transition: background-color 0.3s ease; }
  .button--blue:hover {
    background-color: #0071C2; }

.button--small {
  font-size: 1.2rem;
  line-height: 28px;
  height: 30px;
  padding: 0 27px; }

.body {
  font-size: 1.4rem;
  line-height: 1.6;
  color: #0A3163;
  font-family: "Montserrat", sans-serif;
  font-weight: 400; }

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.4;
  font-family: "Montserrat", sans-serif;
  font-weight: 600; }

p {
  margin: 0 0 1.8rem 0; }

/* Header
   ============================================================== */
.header {
  background: white; }

.phones__select-list {
  position: absolute;
  min-width: 200px;
  padding: 0 12px 10px 12px;
  margin: 0;
  list-style: 0;
  list-style-type: none;
  height: 22px;
  overflow: hidden;
  background: #fff;
  position: absolute;
  top: 38%;
  left: 9px;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 1.3rem;
  border-radius: 20px;
  z-index: 10; }
  .phones__select-list:hover {
    height: auto; }
  .phones__select-list::after {
    content: '';
    position: absolute;
    top: 0;
    right: 9px;
    height: 22px;
    width: 22px;
    background: #C8E4F9 url(../images/icons/menu_angel_arrow.svg) no-repeat center 8px;
    background-size: 12px;
    border-radius: 50%;
    z-index: 11;
    cursor: pointer; }

.phones__item {
  margin-bottom: 15px; }
  .phones__item:first-child {
    margin-bottom: 21px; }

.phones__item-link {
  position: relative;
  padding-left: 35px;
  line-height: 22px;
  color: #174682;
  display: block; }
  .phones__item-link:active, .phones__item-link:focus {
    color: #174682; }
  .phones__item-link::before {
    content: '';
    position: absolute;
    top: calc(50% - 11px);
    left: 2px;
    height: 22px;
    width: 22px;
    background-repeat: no-repeat;
    background-position: 0 0; }

.phones__item-link_tur::before {
  background-image: url(../images/icons/flag_tur.svg); }

.phones__item-link_kaz::before {
  background-image: url(../images/icons/flag_kaz.svg); }

.phones__item-link_ukr::before {
  background-image: url(../images/icons/flag_ukr.svg); }

.phones__item-link_rus::before {
  background-image: url(../images/icons/flag_rus.svg); }

.topbar__container {
  position: relative; }
  .topbar__container::after {
    content: '';
    position: absolute;
    left: 4px;
    bottom: -1px;
    display: block;
    height: 1px;
    width: calc(100% - 8px);
    background-color: #C6E7FF; }

.topbar__phones {
  min-width: 170px; }

.topbar__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 5px;
  height: 57px; }

.menu {
  margin: 0;
  padding: 11px 7px 20px;
  list-style: none;
  display: flex;
  justify-content: space-between; }
  .menu__item {
    position: relative; }
  .menu__item-link {
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    color: #325A8B;
    display: block;
    transition: color 0.3s ease; }
    .menu__item-link:active, .menu__item-link:visited {
      color: #325A8B; }
    .menu__item-link:hover {
      color: #FF8617; }
  .menu__item_dropdown {
    position: relative;
    padding-right: 14px; }
    .menu__item_dropdown:hover > .menu__dropdown {
      display: block; }
    .menu__item_dropdown::after {
      content: '';
      position: absolute;
      top: calc(50% - 2px);
      padding-left: 8px;
      right: 0px;
      height: 8px;
      width: 8px;
      display: block;
      background-image: url("../images/icons/menu_angel_arrow.svg");
      background-repeat: no-repeat;
      background-position: center center; }
  .menu__dropdown {
    font-size: 1.3rem;
    position: absolute;
    left: -32px;
    top: 18px;
    flex-direction: column;
    background-color: white;
    padding: 14px 32px 30px;
    width: 273px;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
    display: none; }
    .menu__dropdown .menu__item {
      margin-bottom: 7px; }
      .menu__dropdown .menu__item:last-child {
        margin-bottom: 0; }
      .menu__dropdown .menu__item::before {
        content: "";
        background-color: #FF8617;
        width: 5px;
        height: 5px;
        display: block;
        position: absolute;
        top: 50%;
        left: -14px;
        border-radius: 50%; }

/* Footer
   ============================================================== */
.footer {
  background-color: #061F3F; }
  .footer__top {
    padding-top: 100px; }
  .footer__list {
    padding: 0;
    margin: 0;
    list-style: none; }
  .footer__list-item {
    position: relative;
    padding-left: 17px;
    margin-bottom: 20px; }
    .footer__list-item:last-child {
      margin-bottom: 0; }
    .footer__list-item::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: 7px;
      height: 7px;
      background-color: #FF8617;
      border-radius: 50%; }
  .footer__link {
    color: white;
    text-decoration: underline;
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    transition: color 0.3s ease; }
    .footer__link:hover {
      color: #FF8617; }
    .footer__link:active, .footer__link:focus {
      color: white; }
